import React from 'react'
import { withPrefix } from 'gatsby'

// material-ui
import AppBar from '@material-ui/core/AppBar/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import Tabs from '@material-ui/core/Tabs'

// custom components
import SEO from '../SEO/SEO'
import Link, { TabLink } from '../Links/Link'

// hook queries site meta data
import useSiteMetaData from '../../data/useSiteMetaData'

/**
 * Header component for the site
 * @param {Object} props - component properties
 * @param {String} props.title - the title of the article
 * @param {String} props.description - the description of the article (excerpt)
 * @param {Object} props.location - representing where the app is from a router perspective
 * @param {*} props.children - children components
 * @returns {*}
 * @constructor
 */
function SiteHeader({ title, description, location, children }) {
  const siteMeta = useSiteMetaData()
  const { defaultTitle, navigationLinks } = siteMeta
  // create a new array with nav uris
  const navigationURIs = navigationLinks.map(nav => withPrefix(nav.uri))
  // check whether current location path exists in navigation uri's array
  const isPathInNavigation = navigationURIs.find(value => {
    return location.pathname.startsWith(value)
  })

  // if not exist, provide a falsy value to the tabs navigator
  const tabsValue = isPathInNavigation ? isPathInNavigation : false
  return (
    <React.Fragment>
      <SEO title={title} description={description} siteMeta={siteMeta} />
      <AppBar position="fixed">
        <Toolbar>
          <Link color="inherit" to={'/'}>
            <Typography variant="h6" color="inherit">
              {defaultTitle}
            </Typography>
          </Link>
        </Toolbar>
        <Tabs value={tabsValue} centered aria-label="nav tabs example">
          {navigationLinks.map(link => (
            <TabLink key={link.uri} value={withPrefix(link.uri)} label={link.name} to={link.uri} />
          ))}
        </Tabs>
      </AppBar>
      {children}
    </React.Fragment>
  )
}

export default SiteHeader
